.Entete {
    border: 1px solid rgb(12, 12, 12);
    background-color: rgb(12, 12, 12);
    color: lightgrey;
    font-size: 2em;
    ul {
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        li {

            display: flex;
            justify-content: center;
            align-items: center;
        }
        img{
            clip-path: circle(50% at 50% 50%);
            margin-left: 20px;
            width: 50px;
            height: 50px;
        }
    }
}